@tailwind base;
@tailwind components;
@tailwind utilities;


li {
    cursor: pointer;
    padding: 0 13px;
    margin-right: 5px;
}

.content-div {
    backgorund-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
}

.content-div:hover {
    background-image: linear-gradient(
        to right,
        rgba(112, 157, 255, 0.8),
        hsla(242, 74%, 61%, 0.8)   
    )!important;
}
